@import url('https://fonts.googleapis.com/css?family=Song+Myung');

:root {
  --navy: rgb(0, 52, 112);
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,0.7)), to(rgba(0,0,0,0.7))), url(Assets/dates-background.jpeg);
  background: -webkit-linear-gradient(rgba(0,0,0,0.7), rgba(0,0,0,0.7)), url(Assets/dates-background.jpeg);
  background: -o-linear-gradient(rgba(0,0,0,0.7), rgba(0,0,0,0.7)), url(Assets/dates-background.jpeg);
  background: linear-gradient(rgba(0,0,0,0.7), rgba(0,0,0,0.7)), url(Assets/dates-background.jpeg);
  background-size: cover;
  background-repeat: none;
  background-attachment: fixed;
}
.App {
  background: #fff;
  margin: 44px auto;
  max-width: 600px;
  border-radius: 5px;
  -webkit-box-shadow: 0 1px 5px #000;
          box-shadow: 0 1px 5px #000;
}
.container {
  padding: 22px;
}
h1, h2, h3 {
  font-family: 'Song Myung', serif;
  color: var(--navy);
}
h1 {
  font-size: 2.7em;
  margin: 0;
  text-align: center;
}

.date-picker {
  width: 100%;
  background: -webkit-gradient(linear, left top, left bottom, from(var(--navy)), to(rgb(0, 30, 65)));
  background: -webkit-linear-gradient(top, var(--navy), rgb(0, 30, 65));
  background: -o-linear-gradient(top, var(--navy), rgb(0, 30, 65));
  background: linear-gradient(to bottom, var(--navy), rgb(0, 30, 65));
  text-align: center;
}
.date-picker h2 {
  color: #fff;
  display: inline-block;
}

.date-picker-form {
  margin-bottom: 22px;
  display: inline-block;
  margin: 0 22px;
}

select,
input,
button {
  background: #fff;
  height: 2em;
  font-size: 1em;
  margin: 0 0.5rem;
  font-family: 'Song Myung', serif;
}


